"use client";

import { useEffect } from "react";
import { useRouter } from "next/navigation";
import { Loader2 } from "lucide-react";
import { useAuthStore } from "@/store/authStore";

interface ProtectedRouteProps {
  children: React.ReactNode;
}

export default function ProtectedRoute({ children }: ProtectedRouteProps) {
  const router = useRouter();
  const { isAuthenticated, isLoading, checkAuth } = useAuthStore();

  useEffect(() => {
    // ุงูุชุญูู ูู ุงูุฌูุณุฉ ุนูุฏ ุชุญููู ุงููููู
    checkAuth();
  }, [checkAuth]);

  useEffect(() => {
    // ุฅุนุงุฏุฉ ุงูุชูุฌูู ุฅูู ุตูุญุฉ ุชุณุฌูู ุงูุฏุฎูู ุฅุฐุง ูู ููู ูุณุฌูุงู
    if (!isLoading && !isAuthenticated) {
      router.push("/login");
    }
  }, [isAuthenticated, isLoading, router]);

  // ุนุฑุถ Loading ุฃุซูุงุก ุงูุชุญูู
  if (isLoading || !isAuthenticated) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="text-center">
          <Loader2 className="w-12 h-12 text-primary animate-spin mx-auto mb-4" />
          <p className="text-gray-600">ุฌุงุฑู ุงูุชุญูู ูู ุงูุฌูุณุฉ...</p>
        </div>
      </div>
    );
  }

  // ุนุฑุถ ุงููุญุชูู ุงููุญูู
  return <>{children}</>;
}

